.slider-left {
    margin-left: 0;

    @media screen and (max-width: 458px) {
        transform: translateX(-15px);
    }
}

.slider-right {
    margin-right: 0;

    @media screen and (max-width: 458px) {
        transform: translateX(15px);
    }
}

// Контейнер слайдеру Swiper, за класом якого ініціалізован слайдер
.slider-mini {
    width: 284px;
    margin-bottom: 20px;

    // @media screen and (min-width: 458px) {
    //     width: 428px;
    //     margin-bottom: 0px;
    // }
    
    @include tablet {
        width: 428px;
    }

    @include desktop {
        width: 548px;
    }

    // автоширина слайду
    &__slide {
        min-width: fit-content;
        object-fit: cover;
        flex-shrink: 1;       
    }
}

// Навігація слайдеру slider-mini, контейнер для стрілок
.slider-nav.slider-mini-nav {
    position: static;
    margin-top: 20px;
    justify-content: flex-start;

    @include tablet {
        margin-top: 32px;
    }

    @include desktop {
        margin-top: 40px;
    }
}

.slider-mini.tripleroom {
    justify-content: flex-end;

    @include tablet {
        justify-content: flex-start;
     }
}